import React, { useContext, useState } from 'react'
import { Card, CardBody, CardHeader, Col, Container, Button, CardFooter, Form, FormGroup, Input, Label, Row} from 'reactstrap';
import { UserContext } from './UserContext';
import "react-toastify/dist/ReactToastify.min.css"
import { toast } from 'react-toastify';

const  Signup = ({updateModalStates}) => {
   

    const {user,setUser} = useContext(UserContext)
  

    const handleChange = (e) => {
        const value=e.target.value;
        setUser({...user,[e.target.name]:value});
    }

    const handleSubmit = (e) => {
        e.preventDefault();
        toast("Form submitted",{
            type:"success", 
        });
    }


    return (
        <Container className="text-center">
            <Row>
                <Col className="offset-lg-1 mt-5">
                    <Card>
                        {/* //TODO: add handlesubmit */}
                        <Form onSubmit={ handleSubmit }>
                          <CardHeader className="modal-title">Signup Here</CardHeader>
                           
                            <CardBody>
                            <FormGroup row>
                                <Label for="name"  sm={3}>
                                        Name
                                    </Label>
                                    <Col sm={9}>
                                        <Input
                                        type="text"
                                        name="name"
                                        id="name"
                                        placeholder="Enter your name"
                                        value={user.name}
                                        onChange={handleChange} />
                                    </Col>
                                </FormGroup>
                                <FormGroup row>
                                    <Label for="email"  sm={3}>
                                        Email
                                    </Label>
                                    <Col sm={9}>
                                        <Input
                                        type="email"
                                        name="email"
                                        id="email"
                                        placeholder="Enter your mail"
                                        value={user.email}
                                        onChange={handleChange} />
                                    </Col>
                                </FormGroup>
                                <FormGroup row>
                                <Label for="password"  sm={3}>
                                        Password
                                    </Label>
                                    <Col sm={9}>
                                        <Input
                                        type="password"
                                        name="password"
                                        id="password"
                                        placeholder="Enter your Password"
                                        value={user.password}
                                        onChange={handleChange} />
                                    </Col>
                                </FormGroup>
                                <FormGroup row>
                                <Label for="qualification"  sm={3}>
                                        Qualification
                                    </Label>
                                    <Col sm={9}>
                                        <Input
                                        type="text"
                                        name="qualification"
                                        id="qualification"
                                        placeholder="Enter your Qualification"
                                        value={user.qualification}
                                        onChange={handleChange } />
                                    </Col>
                                </FormGroup>
                                <FormGroup row>
                                <Label for="fathername"  sm={3}>
                                        Father Name
                                    </Label>
                                    <Col sm={9}>
                                        <Input
                                        type="text"
                                        name="fathername"
                                        id="fathername"
                                        placeholder="Enter your Father Name"
                                        value={user.fathername}
                                        onChange={handleChange} />
                                    </Col>
                                </FormGroup>
                                <FormGroup row>
                                <Label for="mothername"  sm={3}>
                                        Mother Name
                                    </Label>
                                    <Col sm={9}>
                                        <Input
                                        type="text"
                                        name="mothername"
                                        id="mothername"
                                        placeholder="Enter your Mother Name"
                                        value={user.mothername}
                                        onChange={handleChange} />
                                    </Col>
                                </FormGroup>
                                <FormGroup row>
                                <Label for="collegename"  sm={3}>
                                        College Name
                                    </Label>
                                    <Col sm={9}>
                                        <Input
                                        type="text"
                                        name="collegename"
                                        id="collegename"
                                        placeholder="Enter your College Name"
                                        value={user.collegename}
                                        onChange={handleChange} />
                                    </Col>
                                </FormGroup>
                                <FormGroup row>
                                <Label for="city"  sm={3}>
                                        city
                                    </Label>
                                    <Col sm={9}>
                                        <Input
                                        type="text"
                                        name="city"
                                        id="city"
                                        placeholder="Enter your City"
                                        value={user.city}
                                        onChange={handleChange} />
                                    </Col>
                                </FormGroup>
                                <FormGroup row>
                                <Label for="state"  sm={3}>
                                        State
                                    </Label>
                                    <Col sm={9}>
                                        <Input
                                        type="text"
                                        name="state"
                                        id="state"
                                        placeholder="Enter your state"
                                        value={user.state}
                                        onChange={handleChange} />
                                    </Col>
                                </FormGroup>
                            </CardBody>
                            
                            
                            <CardFooter>
                <Button type="submit" onClick={() => updateModalStates(false)} block color="primary">
                  SignUp
                </Button>
              </CardFooter>
             
                        </Form>
                    </Card>
                </Col>
            </Row>
        </Container>
    )
}

export default Signup;
