import React, { useState } from 'react'
import Signup from './Signup';
import Login from './Login';
import Modal from 'react-modal'
import {Button} from 'reactstrap'

function OpenModal({login,open}) {
  const [isModalOpen, setIsModalOpen] = useState(open)

  const updateModalState = (modalState) => {
    setIsModalOpen(modalState);
  }


  const page = login ? <Login updateModalStates={updateModalState}/> : <Signup updateModalStates={updateModalState}/>;

    return (
        <div>
             <Modal isOpen={isModalOpen} 
      onRequestClose={() => setIsModalOpen(false)}
      style={
        {
          overlay:{
            backgroundColor:'grey'
          },
          content:{
            color:'orange'
          }
        }
      }>
        {page}
        {/* <Button className="ml-auto" onClick={() => setIsModalOpen(false)}>Close</Button> */}
      </Modal>
           
        </div>
    )
}

export default OpenModal
