import React, { useState } from 'react'
import {Navbar,Nav,NavItem,NavLink,NavbarBrand, NavbarToggler, Collapse} from 'reactstrap'
import '../App.css';
import {FaUserCircle} from 'react-icons/fa'



const Header = ({updateState}) => {

    const [isOpen,setIsOpen] = useState(false);

    

    const toggle = () => setIsOpen(!isOpen);
    return (
        <div>
        <Navbar color="light" className="bg-dark" expand="md">
        <NavbarBrand href="/Home">Home</NavbarBrand>
        <NavbarToggler onClick={toggle} />
        <Collapse isOpen={isOpen} navbar>
        <Nav className="ml-auto" navbar>
          <NavItem className="link">
            <NavLink onClick={() => updateState(true,false,false)}>SignUp</NavLink>
          </NavItem>
          <NavItem className="link">
            <NavLink onClick={() => updateState(true,true,false)}>LogIn</NavLink>
          </NavItem>
          <NavItem className="link">
            <NavLink onClick={() => updateState(false,false,true)}><FaUserCircle size="2em"/></NavLink>
          </NavItem>
        </Nav>
        </Collapse>
      </Navbar>
        </div>
    )
}

export default Header
