import React, { useContext } from 'react'
import { UserContext } from './UserContext'

const Profile = () => {
    const {user,setUser} = useContext(UserContext)
    return (
        <div>
            <h1>Profile Page</h1>
            <h3>Data</h3>
            <p>
              Name:{user.name} <br/>
                email:{user.email} <br/>
                qualification:{user.qualification} <br/>
                fathername:{user.fathername} <br/>
                mothername:{user.mothername}  <br/>
                collegename:{user.collegename} <br/>
                city:{user.city} <br/>
                state:{user.state} <br />
            </p>
        </div>
    )
}

export default Profile
