import logo from './logo.svg'
import 'bootstrap/dist/css/bootstrap.min.css';
import {Button, Nav,NavLink,  Navbar, NavbarBrand, NavItem, Collapse} from 'reactstrap';

import {ToastContainer} from 'react-toastify';
import './App.css';
import Profile from './components/Profile';
import Signup from './components/Signup';
import Login from './components/Login';
import Header from './components/Header'
import PageNotFound from './components/PageNotFound';
import Home from './components/Home';
import { UserContext } from './components/UserContext';
import { useState } from 'react';
import OpenModal from './components/OpenModal'

const App = () => {
  const [user,setUser] = useState({
    name:"",
    email:"",
    password:"",
    qualification:"",
    fathername:"",
    mothername:"",
    collegename:"",
    city:"",
    state:""
  })

  const [open,setOpen] = useState(false)
  const [login,setLogin] = useState()
  const [profile,setProfile] = useState(false)

  const updateStates = (open,login,profile) => {
    setOpen(open);
    setLogin(login);
    setProfile(profile);
  }

  return (

      <UserContext.Provider value={{user,setUser}} >
      <Header updateState={updateStates}/>
      <ToastContainer />
      {login === true  && <OpenModal login={true} open={open} />}
      {login === false  && <OpenModal login={false} open={open} />}
      {profile === true && <Profile />}
      </UserContext.Provider>
  );
}

export default App;
