import React, { useState } from 'react'
import { toast } from 'react-toastify'
import { Card, CardBody, CardHeader, Col, Container, Button, CardFooter, Form, FormGroup, Input, Label, Row } from 'reactstrap';
import "react-toastify/dist/ReactToastify.min.css"


const Login = ({updateModalStates}) => {
    const [user,setUser] = useState()
    const [email,setEmail] = useState()
    const handleSubmit=(e) =>{
        e.preventDefault();
        toast("Login feature not added yet!",{type:"info"});
    }
    return (
        <div>
             <Container className="text-center">
            <Row>
                <Col className="offset-lg-1 mt-5">
                    <Card>
                        {/* //TODO: add handlesubmit */}
                        <Form onSubmit={ handleSubmit }>
                            <CardHeader className="">Signup Here</CardHeader>
                            <CardBody>
                            <FormGroup row>
                                <Label for="name"  sm={3}>
                                        Name
                                    </Label>
                                    <Col sm={9}>
                                        <Input
                                        type="text"
                                        name="name"
                                        id="name"
                                        placeholder="Enter your name"
                                        value={user}
                                        onChange={e => setUser(e.target.value)} />
                                    </Col>
                                </FormGroup>
                                <FormGroup row>
                                    <Label for="email"  sm={3}>
                                        Email
                                    </Label>
                                    <Col sm={9}>
                                        <Input
                                        type="email"
                                        name="email"
                                        id="email"
                                        placeholder="Enter your mail"
                                        value={email}
                                        onChange={e=>setEmail(e.target.value)} />
                                    </Col>
                                </FormGroup>
                                </CardBody>
                            <CardFooter>
                <Button type="submit" onClick={() => updateModalStates(false)} block color="primary">
                  Login
                </Button>
              </CardFooter>
                        </Form>
                    </Card>
                </Col>
            </Row>
        </Container>

        </div>
    )
}

export default Login
